.container {
    display: flex;
    justify-content: center;
}
  
.review-page {
    text-align: center; 
}
  
.review-page img {
    margin-top: 20px;
    width: auto;
    height: 220px;
    display: block; 
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 15px 0px rgb(240, 158, 218);
}
  
.review-page .review-title {
    border: 1px solid blueviolet;
    border-radius: 10px;
    width: fit-content;
    padding: 8px;
    background-color: blueviolet;
    font-family: 'Courier New', Courier, monospace;
}
  
.back-button {
    width: 150px;
    margin-top: 20px;
    border-radius: 5px; 
    border: 1px solid blueviolet;
    background-color: blueviolet;
    color: white;
    font-family: 'Courier New', Courier, monospace;
}

.back-button:hover {
    background-color: rgb(205, 100, 201);
    border: 1px solid rgb(205, 100, 201);
    box-shadow: 0px 0px 15px 0px rgb(240, 158, 218);
}

@media (max-width: 767px) {
    .review-page .review-title {
        max-width: 270px;
    } 
}