.content-container {
    display: flex;
    align-items: center;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .review-box {
    width: 500px;
    padding: 8px;
    margin-bottom: 10px;
    height: auto;
    min-height: 40px;
    resize: none;
    border-radius: 10px;
    background-color: rgb(201, 159, 240);
    outline: none;
  }
  
  .review-box:focus,
  .review-box:not(:placeholder-shown) {
    background-color: #ccb5e6;
  }
  
  .post-button {
    padding: 8px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 10px;
  }
  
  .post-button:hover {
    background-color: #0056b3;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    position: relative;
    border: 1px solid rgb(201, 159, 240);
    background-color: rgb(201, 159, 240);
    border-radius: 15px;
    padding: 10px;
    margin-bottom: 10px;
    width: 450px;
    max-width: 450px;
    min-height: 40px;
    word-wrap: break-word;
  }

  li button {
    border-radius: 10px;
    padding: 4px;
    background-color: #dc3545;
    color: #fff;
    border: none;
    cursor: pointer;
    margin-left: 350px;
  }
  
  li button:hover {
    background-color: #c82333;
  }
  
  p {
    color: rgb(114, 114, 114);
    margin: 0;
    position: absolute;
    margin-left: 45px;
  }
  
  .profile-picture {
    width: 40px;
    height: auto;
    margin-right: 10px;
  }
  
  @media (max-width: 767px) {
    .review-box {
        max-width: 290px;
        height: 70px;
    } 

    li {
      max-width: 290px;
      min-height: 80px;
    }

    li button {
      width: 100px;
      margin-left: 190px;
    }
}