.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-box {
  position: relative;
  top: 0;
  left: 0;
  width: 200px;
  margin: 20px;
}

.search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(225, 225, 225, 0.51);
  border-radius: 10px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 15px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(225, 225, 225, 0.75);
}

.search-icon {
  position: absolute;
  top: -10%;
  right: -10px;
  transform: translateY(-50%);
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.title {
  border: 1px solid blueviolet;
  border-radius: 10px;
  width: fit-content;
  padding: 8px;
  background-color: blueviolet;
  font-family: 'Courier New', Courier, monospace;
  margin-top: 20px; /* Adjust as needed */
}
