body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: auto; 
    margin: 0px;
    background-color: rgb(179, 111, 241);
}

.album-item {
    border: 1px solid gray;
    border-radius: 10px;
    box-shadow: 0px 0px 15px 0px rgb(240, 158, 218);
    padding: 5px;
    margin: 10px;
    text-align: center;
    width: fit-content;
    max-width: 220px;
    height: auto;
    display: inline-block;
    background-color: rgb(209, 170, 246);
}

.album-item:hover {
    background-color: blueviolet;
}

.album-item img {
    width: 220px;
    height: 200px;
}

.album-item h2 {
    font-size: 12px;
}

